.Bible {
    display: flex;
    justify-content: center;
    align-items: center;
}

.bible-img {
    position: absolute;
    margin: 12% 0 0 0;
    transform: scale(0.5);
    animation: hover 4s infinite;
}

@keyframes hover {
    0% {top: 15%;}
    50% {top: 18%;}
    100% {top: 15%;}
}